@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald Medium";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Oswald Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  height: 100%;
}

body {
  min-width: 320px;
  min-height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.promo {
  width: 100%;
  min-height: 696px;
}
@media (min-width: 1200px) {
  .promo {
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 50% 100%;
    background-position: 100% 0%;
  }
}

.container {
  min-width: 320px;
  height: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 60px;
  }
}

.btn {
  width: 280px;
  height: 40px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #68B738;
  cursor: pointer;
  transition: 0.25s;
}
.btn:hover {
  background-color: #5EAA2F;
}

.header {
  width: 100%;
  height: 66px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    height: 109px;
  }
}
@media (min-width: 1200px) {
  .header {
    background-color: transparent;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
}
@media (min-width: 768px) {
  .header__inner {
    padding-top: 24px;
  }
}

.header__logo-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 191px;
}
@media (min-width: 768px) {
  .header__logo-group {
    width: 174px;
  }
}

.header__logo-img {
  width: 100%;
}

@media (min-width: 768px) {
  .header__logo-text {
    display: none;
  }
}

.navbar-wrapper {
  display: none;
}
@media (min-width: 768px) {
  .navbar-wrapper {
    display: block;
  }
}

.navbar__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 440px;
}

.navbar__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.25s all;
}
.navbar__link:hover {
  opacity: 0.6;
}
@media (min-width: 1200px) {
  .navbar__link {
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.intro {
  min-height: 480px;
  max-height: 700px;
  width: 100%;
  padding: 0;
}
@media (max-width: 400px) {
  .intro {
    background-image: url("../img/index-background-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-size: 100% 70%;
  }
}
@media (min-width: 768px) {
  .intro {
    padding-left: 87px;
  }
}
@media (min-resolution: 2dppx) and (min-width: 320px) and (max-width: 400px) {
  .intro {
    background-image: url("../img/index-background-mobile@2x.jpg");
  }
}
.intro__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 27px;
}
@media (min-width: 400px) {
  .intro__inner {
    align-items: flex-start;
    padding-top: 40px;
  }
}
.intro__text {
  max-width: 396px;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 400px) {
  .intro__text {
    text-align: left;
    color: #000000;
  }
}
@media (min-width: 500px) {
  .intro__text {
    max-width: 414px;
  }
}
.intro__title {
  padding-bottom: 40px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 500px) {
  .intro__title {
    font-size: 60px;
    line-height: 60px;
  }
}
.intro__subtitle {
  margin-bottom: 29px;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}
@media (min-width: 500px) {
  .intro__subtitle {
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 401px) {
  .intro__img {
    position: absolute;
    top: 88%;
    left: 21%;
  }
}
@media (min-width: 600px) {
  .intro__img {
    top: 94%;
    left: 0;
  }
}
@media (min-width: 768px) {
  .intro__img {
    top: 240px;
    left: -87px;
  }
}
@media (min-width: 1200px) {
  .intro__img {
    left: 30%;
  }
}
@media (min-width: 401px) and (max-width: 1200px) {
  .intro__btn {
    position: absolute;
    top: 181px;
    left: 0;
  }
}
@media (min-width: 400px) {
  .intro__btn {
    width: 200px;
    height: 44px;
  }
}
@media (min-width: 500px) {
  .intro__btn {
    top: 237px;
  }
}
@media (min-width: 768px) {
  .intro__btn {
    top: 267px;
    width: 244px;
    height: 54px;
  }
}